.tb_g_post_container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin: 0 auto;
    overflow: hidden;
}

.tb_g_post_wrapper{
    padding: 20px;
    width: 20%;
}

.tb_g_post_in{
    display: block;
    position: relative;
    overflow: hidden;
    background-color: var(--tb-bg-color);
    border-radius: var(--tb-border-radius);
    border: 1px solid #0000001a;
    cursor: pointer;
}

.tb_g_contant_wrapper {
    padding: 15px;
}

.tb_g_media_wrap{
    width: 100%;
    position: relative;
}

.tb_g_post_media_wrapp{
    position: relative;
}

.tb_g_media_icon.tb__media_ico_c .tb_center_video_ico{
    font-size: 30px;
}

.tb_g_image_wrap_{
    padding-bottom: 100%;
    position: relative;
}
.tb_g_image, .tb_g_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
}
.tb_g_video{
    background-color: #000;
}

.tb_g_video video,
.tb_g_video iframe{
    object-fit: cover;
    object-position: center;
}

.tb_g_video > div {
    display: flex;
    align-items: center;
    justify-content: center;
}

.tb_g_share_container{
    z-index: 4;
}

.tb_g_author_wrapper {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

.tb_g_author {
    width: calc(100% - 30px);
}

.tb_g_author_profile {
    height: 40px;
    width: 40px;
    border-radius: 4px;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    overflow: hidden;
}
.tb_g_author_profile__{
    height: 100% !important;
    width: 100% !important;
    margin: 0;
    object-fit: cover;
    object-position: center;
}

.tb_g_authorname {
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 4px;
    text-align: left;
    white-space: nowrap;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_g_author {
    display: flex;
    align-items: center;
    flex-grow: 1;
}

.tb_g_author_info {
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: calc(100% - 54px);
    padding-right: 10px;
}

.tb_g_post_info {
    display: flex;
    align-items: center;

}

.tb_g_authorname {
    font-family: var(--tb-author-font);
    color: var(--tb-author-color);
}

.tb_g_time {
    font-family: var(--tb-font);
    color: var(--tb-author-color);
}

.tb_g_content,
.tb_g_rating_content {
    font-family: var(--tb-font);
    color: var(--tb-color);
    font-size: var(--tb-font-size);
    line-height: var(--tb-line-height);
}

.tb_g_time {
    font-size: 10px;
    font-weight: 500;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-height: normal;
    max-width: 100%;
}
.tb_g_social_ico{
    color: var(--tb-icon-color);
}

.tb_g_content {
    margin-bottom: 15px;
    white-space: pre-line;
    word-break: break-word;
}
.tb_g_rating_content{
    margin-bottom: 15px;
}
.tb_g_rating_content, .tb_g_text_post {
    text-align: center;
}

.tb_g_p_wrp{
    display: flex;
    align-items: center;
    padding: 8px;
    background-color: rgba(226, 226, 226, 0.30);
    width: 100%;
    border-radius: 4px;
}

.tb_g_p_{
    height: 60px;
    margin-right: 10px;
    border-radius: 4px;
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-basis: 60px;
}

.tb_g_p_img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.tb_g_p_t{
    font-size: 14px;
    margin-bottom: 5px;
    overflow-wrap: anywhere;
}

.tb_g_rating__{
    margin-bottom: 15px;
}

.tb_c_p__{
    width: 100%;
    height: auto;
}


.tb_g_image_wrap_ {
    padding-bottom: 100%;
    position: relative;
}

.tb_g_image, .tb_g_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    margin: 0;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
}

.tb_cp_video{
    background-color: #000;
}

.tb_cp_video video,
.tb_cp_video iframe{
    object-fit: cover;
    object-position: center;
}