.tb_c_p__arrow {
       position: absolute;
       top: calc(50% - 16px);
       background-color: #fff;
       border-radius: 50%;
       height: 28px;
       width: 28px;
       font-size: 12px;
       display: flex;
       text-align: center;
       align-items: center;
       justify-content: center;
       color: #000;
       opacity: 0.6;
       border: none !important;
       z-index: 9;
       cursor: pointer;
       box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.3);
       transition: opacity .3s ease-in;
}

.tb_c_p__arrow_right__ {
       right: 12px;
       left: auto;
}

.tb_c_p__arrow_left__ {
       left: 12px;
       right: auto;
}

.tb_c_p__arrow:hover {
       opacity: 1;
}

.tb_c_p__arrow.swiper-button-disabled {
       display: none !important;
}

.tb_c_p__dots {
       position: absolute;
       bottom: 15px;
       text-align: center;
       list-style: none;
       padding: 0;
       width: 100%;
       margin: 0;
}

.tb_c_p__dots li {
       display: inline-block;
       padding: 2.5px;
}

.tb_c_p__dots li .tb_c_p__dot {
       font-size: 0;
       background-color: #fff !important;
       height: 7px !important;
       width: 7px !important;
       border-radius: 50% !important;
       display: block !important;
       padding: 0 !important;
       border: none !important;
       cursor: pointer;
       box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.1);
       opacity: .5 !important;
}

.tb_c_p__dots li .tb_c_p__dot.is-active {
       opacity: 1 !important;
}